<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import axios from 'axios';
import {Form,HasError} from 'vform'
import FormModal from '../../components/modals/store/show';
import Datepicker from 'vuejs-datepicker';
import moment from 'moment'
import CategoryModal from '../../components/modals/category/add';
import ProductModal from '../../components/modals/product/add';


export default {
  components: {
    LayoutDefault,PaginationNav,HasError,Datepicker,FormModal,
    CategoryModal,ProductModal,
  },
  data() {
        return {
            form: new Form({
                id:null,created_at:new Date().toLocaleString(),
                tax:null,pay_at:new Date(),confirmed_at:null,
                deposit:null,deposit_at:new Date(),tax_at:new Date(),
                file1:null,file2:null,file3:null,pay_left:null,
                product_id:null,price:null,months:null,pay_for_month:null,
                check:1,
            }),
            categories:[],
            products:[],product:null,
            loader:false,
            file1:null,file2:null,file3:null,
        }
    },
    created() { 
        this.fetchData();
    },
    methods: {
        onFileRemove(num) {
            switch(num) { 
                case 1: { 
                    this.form.file1=null;this.file1=null
                    break; 
                } 
                case 2: { 
                    this.form.file2=null;this.file2=null
                    break; 
                } 
                case 3: { 
                    this.form.file3=null;this.file3=null
                    break; 
                } 
            } 
        },
        onFileChange(e,num) {
            var files = e.target.files || e.dataTransfer.files;
            if (files.length){
                var file=URL.createObjectURL(files[0])
                switch(num) { 
                    case 1: { 
                        this.form.file1=files[0]
                        this.file1=file
                        break; 
                    } 
                    case 2: { 
                        this.form.file2=files[0]
                        this.file2=file
                        break; 
                    } 
                    case 3: { 
                        this.form.file3=files[0]
                        this.file3=file
                        break; 
                    } 
                } 
            }
        },
       
        selectCategory(products) {
            this.products=(products)?products:[];
            this.selectProduct(null)
            this.product=null
        },
        selectProduct(product) {
            this.form.product_id=(product)?product.id:null;
            this.form.price=(product)?(product.price/1):null;
        },
        
        async fetchData() {
            await axios.get('/api/client_orders/create').then(({data}) => {
                this.categories=data.success.categories;
            });
        },
       
        async saveOrder() {
            await  this.form.post('/api/client_orders').then(({data}) => {
                if(data.exists){this.exists();}
                if(this.form.file1||this.form.file2||this.form.file3){
                    let fd = new FormData();
                    fd.append("order_id", data.success);
                    if(this.form.file1){
                        fd.append("file1", this.form.file1);
                    }
                    if(this.form.file2){
                        fd.append("file2", this.form.file2);
                    }
                    if(this.form.file3){
                        fd.append("file3", this.form.file3);
                    }
                    axios.post('/api/client_orders_image', fd, {
                        headers: {
                        'Content-Type': 'multipart/form-data'
                        }
                    })
                }
                this.$router.push({ name: "client_orders_show",params:{order_id:data.success}})
            })
        },
        exists() {
            this.$swal.fire({
                title:'تحذير',text:"هذا العميل موجود مسبقا",
                icon: 'warning',showCancelButton: true,confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',cancelButtonText: "رجوع",
                confirmButtonText: "أستمرار",
            }).then((result) => {
                if (result.value) {
                    this.form.check=0;
                    this.saveOrder();
                }
            })
        },
    },
}
</script>

