<template>
    <!--begin::Modal-->
    <div class="modal fade" id="formModal" data-bs-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <form @submit.prevent="hide" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="formModalLabel">تفاصيل المنتج</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body row">
                    <div class="col-12 mt-3" v-for="detail in details">
                        <label>{{detail.option.name}}</label>
                        <input :value="(detail.option_value)?detail.option_value.name:detail.value" type="text" class="form-control" disabled>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="submit" class="btn btn-light">رجوع</button>
                </div>
            </form>
        </div>
    </div>
    <!--end::Modal-->
</template>
<script>
export default {
    data() {
        return {
            details: null,
        };
    },
    methods: {
        show(details) {
            this.details=details;
            $('#formModal').modal('show');
        },
        hide() {
            $('#formModal').modal('hide');
        },
    },
}
</script>
